/* Import the monospaced font (if not already imported in index.html, you can use either method) */
/* @import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;700&display=swap'); */

/* Reset default browser margins and set a global font */
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto Mono', monospace;
  background-color: #f4f6f8; /* Light background color */
  color: #333;
}

/* Styling for links */
a {
  text-decoration: none;
  color: inherit;
}

/* Example: styling your container to center content */
.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
}

